<template >
  <div class="main__week-day">
    <h2 class="main__week-title">{{ getWeatherWeek }}</h2>
    <p class="main__week-dayInfo">{{ getWeatherDay }} {{ getWeatherMonth }}</p>
    <div class="main__week-img">
      <img
        src="../assets/images/animated/day.svg"
        alt=""
        v-if="description == `clear sky`"
      />
      <img
        src="../assets/images/animated/cloudy-day-1.svg"
        alt=""
        v-else-if="description == `few clouds`"
      />
      <img
        src="../assets/images/animated/cloudy-day-2.svg"
        alt=""
        v-else-if="description == `scattered clouds`"
      />
      <img
        src="../assets/images/animated/cloudy.svg"
        alt=""
        v-else-if="description == `broken clouds`"
      />
      <img
        src="../assets/images/animated/rainy-6.svg"
        alt=""
        v-else-if="description == `	shower rain`"
      />
      <img
        src="../assets/images/animated/rainy-1.svg"
        alt=""
        v-else-if="description == `rain`"
      />
      <img
        src="../assets/images/animated/rainy-4.svg"
        alt=""
        v-else-if="description == `moderate rain`"
      />
      <img
        src="../assets/images/animated/rainy-3.svg"
        alt=""
        v-else-if="description == `light rain`"
      />
      <img
        src="../assets/images/animated/snowy-6.svg"
        alt=""
        v-else-if="description == `snow`"
      />
      <img
        src="../assets/images/animated/snowy-5.svg"
        alt=""
        v-else-if="description == `rain and snow`"
      />
      <img
        src="../assets/images/animated/snowy-1.svg"
        alt=""
        v-else-if="description == `light snow`"
      />
      <img
        src="../assets/images/animated/thunder.svg"
        alt=""
        v-else-if="description == `thunderstorm`"
      />
      <img src="../assets/images/animated/cloudy.svg" alt="" v-else />
    </div>
    <p class="main__week-max">
      {{
        Math.round(day.temp.max) <= 0
          ? Math.round(day.temp.max)
          : "+" + Math.round(day.temp.max)
      }}°
    </p>
    <p class="main__week-min">{{ Math.round(day.temp.min) }}°</p>
    <p class="main__week-descr">{{ day.weather[0].description }}</p>
  </div>
</template>
<script>
import unix from "@/timestamp";
export default {
  props: {
    day: Object,
  },
  computed: {
    description() {
      return this.day.weather[0].description;
    },
    getWeatherWeek() {
      return unix(this.day.dt, "weekday");
    },
    getWeatherMonth() {
      return unix(this.day.dt, "month");
    },
    getWeatherDay() {
      return unix(this.day.dt, "day");
    },
  },
};
</script>
<style lang="scss">
</style>