function unix(dt, type) {
    const millisecond = dt * 1000
    const data = new Date(millisecond)
    let result =
        type == "weekday" ? data.toLocaleString("en-US", {
            weekday: "long"
        }) :
        type == "month" ? data.toLocaleString("en-US", {
            month: "long"
        }) :
        type == "day" ? data.toLocaleString("en-US", {
            day: "numeric"
        }) : ""
        return result
}
export default unix