<template>
  <nav class="nav">
    <div class="nav__info">
      <a href="#!" class="nav__logo">
        <img src="../assets/images/animated/cloudy-day-1.svg" alt="" />
       Weather
      </a>
      <div class="nav__item">
        <div class="change" @click="change">
          <img
            class="change__img"
            v-if="img"
            src="../assets/images/animated/day.svg"
            alt=""
          />
          <img
            class="change__img"
            v-else
            src="../assets/images/animated/night.svg"
            alt=""
          />
        </div>
        <input
          type="text"
          placeholder="Choose City"
          v-model="search"
          @keydown.enter="getCountry(search)"
        />
      </div>
    </div>
  </nav>
</template>
<script>
import { mapActions, mapMutations, mapState } from "vuex";
export default {
  data() {
    return {
      search: "",
      img: true,
      active: "",
    };
  },
  methods: {
    ...mapMutations(["setClass"]),
    change() {
      this.img = !this.img;
      this.active = this.active == "" ? "active" : "";
      this.setClass(this.active);
    },
    ...mapActions(["getCountry"]),
  },
};
</script>
