<template >
  <div class="app" :class="activeClass">
    <div class="container" v-if="getWeatherInfo">
      <Nav></Nav>
      <Main />
    </div>
    <div class="load" v-else>
      <div class="loader">
        <div class="loader__circle"></div>
      </div>
    </div>
  </div>
</template>
    
<script>
import Main from "./components/Main.vue";
import Nav from "./components/Nav.vue";
import { mapActions, mapGetters, mapState } from "vuex";
export default {
  components: { Nav, Main },
  methods: {
    ...mapActions(["getCountry"]),
  },
  computed: {
    ...mapState(["activeClass"]),
    ...mapGetters(["getWeatherInfo"]),
  },
  created() {
    this.getCountry("Tashkent");
  },
};
</script>
<style lang="scss">
.load {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
  background: rgb(1, 1, 31);
}
.loader {
  width: 200px;
  height: 200px;
  position: relative;
  animation: loading linear infinite 1s;
  &::before {
    content: "";
    width: 20px;
    height: 20px;
    background-color: aqua;
    box-shadow: 0 0 15px aqua, 0 0 30px aqua, 0 0 40px aqua, 0 0 50px aqua,
      0 0 60px aqua, 0 0 70px aqua;
    position: absolute;
    left: 50%;
    top: 0;
    border-radius: 50%;
    transform: translateX(-50%);
    z-index: 11;
  }
  &::after {
    content: "";
    width: 50%;
    height: 100%;
    position: absolute;
    background-image: linear-gradient(
      to top,
      transparent,
      rgba(0, 255, 255, 0.8)
    );
    background-size: 170px 170px;
    background-repeat: no-repeat;
    border-radius: 200px 0 0 200px;
    top: 0;
    left: 0;
    z-index: 7;
  }
  &__circle {
    width: 160px;
    height: 160px;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    z-index: 10;
    border-radius: 50%;
    background: rgb(1, 1, 31);
  }
}
@keyframes loading {
  to {
    transform: rotate(720deg);
  }
}
</style>
