<template>
  <div class="main__content">
    <div class="main__content-left">
      <p class="main__content-deg">
        {{
          Math.round(getWeatherInfo.current.temp) <= 0
            ? Math.round(getWeatherInfo.current.temp)
            : "+" + Math.round(getWeatherInfo.current.temp)
        }}°
      </p>
      <p class="main__content-day">Today</p>
      <p class="main__content-time">Time: {{ getTime }}</p>
      <p class="main__content-city">City: {{ getWeatherInfo.name }}</p>
      <div class="main__content-img">
        <img
          src="../assets/images/animated/day.svg"
          alt=""
          v-if="description == `clear sky`"
        />
        <img
          src="../assets/images/animated/cloudy-day-1.svg"
          alt=""
          v-else-if="description == `few clouds`"
        />
        <img
          src="../assets/images/animated/cloudy-day-2.svg"
          alt=""
          v-else-if="description == `scattered clouds`"
        />
        <img
          src="../assets/images/animated/cloudy.svg"
          alt=""
          v-else-if="description == `broken clouds`"
        />
        <img
          src="../assets/images/animated/rainy-6.svg"
          alt=""
          v-else-if="description == `shower rain`"
        />
        <img
          src="../assets/images/animated/rainy-1.svg"
          alt=""
          v-else-if="description == `rain`"
        />
        <img
          src="../assets/images/animated/rainy-4.svg"
          alt=""
          v-else-if="description == `moderate rain`"
        />
        <img
          src="../assets/images/animated/rainy-3.svg"
          alt=""
          v-else-if="description == `light rain`"
        />
        <img
          src="../assets/images/animated/snowy-6.svg"
          alt=""
          v-else-if="description == `snow`"
        />
        <img
          src="../assets/images/animated/snowy-4.svg"
          alt=""
          v-else-if="description == `rain and snow`"
        />
        <img
          src="../assets/images/animated/snowy-3.svg"
          alt=""
          v-else-if="description == `light snow`"
        />
        <img
          src="../assets/images/animated/thunder.svg"
          alt=""
          v-else-if="description == `thunderstorm`"
        />
        <img src="../assets/images/animated/cloudy.svg" alt="" v-else />
      </div>
    </div>
    <div class="main__content-right">
      <img
        class="main__content-cloud"
        src="../assets/images/cloud.png"
        alt=""
      />
      <div class="main__content-info">
        <div class="main__content-info-img">
          <img src="@/assets/images/temp.svg" alt="" />
        </div>
        <p class="main__content-info-title">Temperature</p>
        <p class="main__content-info-text">
          {{ Math.round(getWeatherInfo.current.temp) }}° - feels like
          {{ Math.round(getWeatherInfo.current.feels_like) }}°
        </p>
      </div>
      <div class="main__content-info">
        <div class="main__content-info-img">
          <img src="@/assets/images/davleniya.svg" alt="" />
        </div>
        <p class="main__content-info-title">Pressure</p>
        <p class="main__content-info-text">
          {{ getWeatherInfo.current.pressure }} mm mercury column
        </p>
      </div>
      <div class="main__content-info">
        <div class="main__content-info-img">
          <img src="../assets/images/yomgir.svg" alt="" />
        </div>
        <p class="main__content-info-title">Humidity</p>
        <p class="main__content-info-text">
          {{ getWeatherInfo.current.humidity }} %
        </p>
      </div>
      <div class="main__content-info">
        <div class="main__content-info-img">
          <img src="../assets/images/wind.svg" alt="" />
        </div>
        <p class="main__content-info-title">Wind</p>
        <p class="main__content-info-text">
          {{ getWeatherInfo.current.wind_speed }} m/s southwest
        </p>
      </div>
    </div>
  </div>
</template>
<script>
import { mapGetters } from "vuex";
export default {
  computed: {
    ...mapGetters(["getWeatherInfo"]),
    description() {
      return this.getWeatherInfo.current.weather[0].description;
    },
    getTime() {
      return new Date().toLocaleString("en-US", {
        timeZone: this.getWeatherInfo.timezone,
        timeStyle:"short",
        hourCycle:"h24"
      });
    },
  },
};
</script>
<style lang="scss">
</style>