import {
  createStore
} from "vuex";
import axios from 'axios'
const store = createStore({
  state: {
    apiKey: "ea8274c23d69472828079f18267fc41b",
    getWeatherInfo: null,
    activeClass: ""
  },
  mutations: {
    setClass(state, payload) {
      state.activeClass = payload
    },
    setWeather(state, payload) {
      state.getWeatherInfo = payload
    }
  },
  actions: {
    async getCountry({
      commit,
      state
    }, city) {
      try {
        let res = await axios.get(`https://api.openweathermap.org/geo/1.0/direct?q=${city}&appid=${state.apiKey}`)
        let cordinate = res.data[0]
        let {
          lat,
          lon,
          name
        } = cordinate
        let res2 = await axios.get(`https://api.openweathermap.org/data/2.8/onecall?lat=${lat}&lon=${lon}&exclude=minutely,alerts,hourly&appid=${state.apiKey}&units=metric`)
        let weather = res2.data
        let weatherInfo = {
          ...weather,
          name
        }
        commit("setWeather", weatherInfo)
        console.log(weatherInfo);
      } catch (error) {
        console.log(error);
      }
    },

  },
  getters: {
    getWeatherInfo: state => state.getWeatherInfo,
    getWeatherDay: state => state.getWeatherInfo.daily
  },
});

export default store;